import React from "react";
import Layout from "../component/Layout/Layout";
import { useReactMediaRecorder } from "react-media-recorder-2";
import toast from "react-hot-toast";

const Screen = () => {
  const { status, startRecording, stopRecording, mediaBlobUrl } =
    useReactMediaRecorder({ screen: true });

  const saveVideo = () => {
    try {
      if (mediaBlobUrl) {
        localStorage.setItem("recordedVideo", mediaBlobUrl);
        toast.success("Video saved to localStorage");
      }
    } catch (error) {
      toast.error("Error saving video to localStorage:", error);
    }
  };
  return (
    <>
      <Layout>
        <div className="video" style={{ marginTop: "50px" }}>
          <video
            src={mediaBlobUrl}
            width={500}
            height={500}
            controls
            autoPlay
          />
        </div>
        <div class="container" style={{ marginTop: "20px" }}>
          <div class="row">
            <div className="col-xs-12">
              <button
                className="btn btn-light"
                onClick={startRecording}
                disabled={status === "recording"}
              >
                Start Recording
              </button>
              <button
                className="btn btn-light"
                onClick={stopRecording}
                disabled={status !== "recording"}
              >
                Stop Recording
              </button>

              <button className="btn btn-warning" onClick={saveVideo}>
                Save
              </button>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
};

export default Screen;
